import {Component, OnInit} from '@angular/core';
declare var $: any;
declare var Morris: any;

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class DashboardComponent implements OnInit {
  constructor() {}

  ngOnInit() {
    Morris.Bar({
      element: 'morris-bar',
      data: [
        {
          x: '2011 Q1',
          y: 3,
          z: 2,
          a: 3,
        },
        {
          x: '2011 Q2',
          y: 2,
          z: null,
          a: 1,
        },
        {
          x: '2011 Q3',
          y: 0,
          z: 2,
          a: 4,
        },
        {
          x: '2011 Q4',
          y: 2,
          z: 4,
          a: 3,
        },
      ],
      xkey: 'x',
      ykeys: ['y', 'z', 'a'],
      labels: ['Y', 'Z', 'A'],
    });
    Morris.Bar({
      element: 'stacked',
      data: [
        {
          x: '2011 Q1',
          y: 3,
          z: 2,
          a: 3,
        },
        {
          x: '2011 Q2',
          y: 2,
          z: null,
          a: 1,
        },
        {
          x: '2011 Q3',
          y: 0,
          z: 2,
          a: 4,
        },
        {
          x: '2011 Q4',
          y: 2,
          z: 4,
          a: 3,
        },
      ],
      xkey: 'x',
      ykeys: ['y', 'z', 'a'],
      labels: ['Y', 'Z', 'A'],
      stacked: true,
    });
    Morris.Area({
      element: 'morris-area-chart',
      data: [
        {
          period: '2010',
          iphone: 50,
          ipad: 80,
          itouch: 20,
        },
        {
          period: '2011',
          iphone: 130,
          ipad: 100,
          itouch: 80,
        },
        {
          period: '2012',
          iphone: 80,
          ipad: 60,
          itouch: 70,
        },
        {
          period: '2013',
          iphone: 70,
          ipad: 200,
          itouch: 140,
        },
        {
          period: '2014',
          iphone: 180,
          ipad: 150,
          itouch: 140,
        },
        {
          period: '2015',
          iphone: 105,
          ipad: 100,
          itouch: 80,
        },
        {
          period: '2016',
          iphone: 250,
          ipad: 150,
          itouch: 200,
        },
      ],
      xkey: 'period',
      ykeys: ['iphone', 'ipad', 'itouch'],
      labels: ['iPhone', 'iPad', 'iPod Touch'],
      pointSize: 3,
      fillOpacity: 0,
      pointStrokeColors: ['#00bfc7', '#fdc006', '#9675ce'],
      behaveLikeLine: true,
      gridLineColor: '#e0e0e0',
      lineWidth: 1,
      hideHover: 'auto',
      lineColors: ['#00bfc7', '#fdc006', '#9675ce'],
      resize: true,
    });
    //// Area Chart
    Morris.Area({
      element: 'morris-area-chart2',
      data: [
        {
          period: '2010',
          SiteA: 0,
          SiteB: 0,
        },
        {
          period: '2011',
          SiteA: 130,
          SiteB: 100,
        },
        {
          period: '2012',
          SiteA: 80,
          SiteB: 60,
        },
        {
          period: '2013',
          SiteA: 70,
          SiteB: 200,
        },
        {
          period: '2014',
          SiteA: 180,
          SiteB: 150,
        },
        {
          period: '2015',
          SiteA: 105,
          SiteB: 90,
        },
        {
          period: '2016',
          SiteA: 250,
          SiteB: 150,
        },
      ],
      xkey: 'period',
      ykeys: ['SiteA', 'SiteB'],
      labels: ['Site A', 'Site B'],
      pointSize: 0,
      fillOpacity: 0.4,
      pointStrokeColors: ['#b4becb', '#01c0c8'],
      behaveLikeLine: true,
      gridLineColor: '#e0e0e0',
      lineWidth: 0,
      smooth: false,
      hideHover: 'auto',
      lineColors: ['#b4becb', '#01c0c8'],
      resize: true,
    });
    // Line Chart
    new Morris.Line({
      // ID of the element in which to draw the chart.
      element: 'myfirstchart',
      // Chart data records -- each entry in this array corresponds to a point on
      // the chart.
      data: [
        {
          year: '2008',
          value: 20,
        },
        {
          year: '2009',
          value: 10,
        },
        {
          year: '2010',
          value: 5,
        },
        {
          year: '2011',
          value: 5,
        },
        {
          year: '2012',
          value: 20,
        },
      ],
      // The name of the data record attribute that contains x-values.
      xkey: 'year',
      // A list of names of data record attributes that contain y-values.
      ykeys: ['value'],
      // Labels for the ykeys -- will be displayed when you hover over the
      // chart.
      labels: ['Value'],
    });
    Morris.Area({
      element: 'extra-area-chart',
      data: [
        {
          period: '2010',
          iphone: 0,
          ipad: 0,
          itouch: 0,
        },
        {
          period: '2011',
          iphone: 50,
          ipad: 15,
          itouch: 5,
        },
        {
          period: '2012',
          iphone: 20,
          ipad: 50,
          itouch: 65,
        },
        {
          period: '2013',
          iphone: 60,
          ipad: 12,
          itouch: 7,
        },
        {
          period: '2014',
          iphone: 30,
          ipad: 20,
          itouch: 120,
        },
        {
          period: '2015',
          iphone: 25,
          ipad: 80,
          itouch: 40,
        },
        {
          period: '2016',
          iphone: 10,
          ipad: 10,
          itouch: 10,
        },
      ],
      lineColors: ['#fb9678', '#01c0c8', '#8698b7'],
      xkey: 'period',
      ykeys: ['iphone', 'ipad', 'itouch'],
      labels: ['Site A', 'Site B', 'Site C'],
      pointSize: 0,
      lineWidth: 0,
      resize: true,
      fillOpacity: 0.8,
      behaveLikeLine: true,
      gridLineColor: '#e0e0e0',
      hideHover: 'auto',
    });
  }
}



